import EditDisabledIcon from "assets/img/buttonIcons/Disabled_Edit.svg";
import EditIcon from "assets/img/buttonIcons/Edit.svg";
import AddNewReceiptDialog, {
  IAddNewReceiptDialogRef,
  ILoginFeeReceiptData,
  MODULE_FOR_ADD_NEW_RECEIPT,
  initialNewReceiptData,
} from "components/AddNewReceiptDialog/AddNewReceiptDialog";
import AutoComplete from "components/AutoComplete/AutoComplete";
import ButtonBox from "components/ButtonBox/ButtonBox";
import DatePicker from "components/DatePicker/DatePicker";
import ErrorDialog from "components/ErrorDialog/ErrorDialog";
import IconButton, { BUTTON_TYPES } from "components/IconButton/IconButton";
import InputBox from "components/InputBox/InputBox";
import MenuBox from "components/MenuBox/MenuBox";
import ReadOnlyTable from "components/ReadOnlyTable/ReadOnlyTable";
import SectionTitle from "components/SectionTitle/SectionTitle";
import SharePaymentDetailsDialog from "components/SharePaymentDetailsDialog/SharePaymentDetails";
import Text from "components/Text/Text";
import WarningDialog from "components/WarningDialog/WarningDialog";
import { RouteProps, withRouter } from "hoc/withRouter";
import { useTranslateText } from "hook/CustomHooks";
import { numberFormatter } from "locale";
import { CalendarChangeParams } from "primereact/calendar";
import { Column } from "primereact/column";
import { ChangeEvent, useEffect, useRef, useState } from "react";
import { useDispatch } from "react-redux";
import { linkAuthRoute, linkBasePath } from "routes";
import StaticMasterDataService from "services/masterdata/StaticMaster/StaticMasterDataService";
import DepositAccountService from "services/transaction/DDE/DepositAccountService";
import FeeService from "services/transaction/FeeService";
import LoanService from "services/transaction/LoanService";
import { LINK_FEES_DEPOSIT_CREATE_EDIT } from "transactionRoutes";
import {
  currencyFormatToNumberForFilter,
  formatDate,
  parseStandardAPIErrorMessage,
} from "utils/CommonUtil";
import {
  BUTTON_TYPE,
  FEE_RECEIPT_STATUS,
  INPUT_STYLE_TYPE,
  STATIC_MASTER_TYPE_TOAST_TEXT,
  TOAST_STYLE_TYPE,
} from "utils/Constant";
import { IDropDownOptions, enrichForNewReceipt } from "views/transaction/PageDDE/Login/Helper";
import { ILoginFeeReceiptRowData } from "views/transaction/PageDDE/Login/PageFees/PageFees";
import { updateReceiptDetailsPageObj } from "../../PageDDE/Login/Helper";
import SystemDatePicker from "components/SystemDatePicker/SystemDatePicker";

interface IAutocomplete {
  name?: string;
  uid?: string;
  accountNumber?: string;
}
interface IMenuModal {
  className: string;
  label: string;
  receiptUid?: string;
  command?: () => void;
  disabled?: boolean;
}

interface IFilter {
  deposit: IAutocomplete;
  depositAmountGreaterThenEqualTo: string;
  depositAmountLessThenEqualTo: string;
  dateOfDepositGreaterThenEqualTo: string;
  dateOfDepositLessThenEqualTo: string;
  sahayyaBankAccount: IAutocomplete;
  loan: IAutocomplete;
  status: string;
  updatedOnGreaterThenEqualTo: string;
  updatedOnLessThenEqualTo: string;
}

export interface IFeeCollectionDepositTableRowData {
  tenantUid: string;
  depositUid: string;
  depositRefNo: string;
  totalAmount: number;
  totalAmountInWords: string;
  depositDate: string;
  holderName: string;
  accountNumber: string;
  depositAccountUid: string;
  updatedAt: string;
  updatedAtSystemDate: string;
  receiptCount: string;
  lanId: string;
}

interface IPageObj {
  showFilter: boolean;
  filter: IFilter;
  tableSearch: string;
  sharePaymentDialog: boolean;
  deleteReceipt: {
    showPopUp: boolean;
    uid: string;
  };
  dropdownOptions: {
    instrumentType: IDropDownOptions[];
    // instrumentType:IDropDownOptions[];
  };
  editReceipt: ILoginFeeReceiptData;
  editReceiptDialog: boolean;
}
const initialFilter: IFilter = {
  deposit: {
    uid: "",
    name: "",
  },
  depositAmountGreaterThenEqualTo: "",
  depositAmountLessThenEqualTo: "",
  dateOfDepositGreaterThenEqualTo: "",
  dateOfDepositLessThenEqualTo: "",
  sahayyaBankAccount: {
    name: "",
    accountNumber: "",
  },
  loan: {
    uid: "",
    name: "",
  },
  status: "",
  updatedOnGreaterThenEqualTo: "",
  updatedOnLessThenEqualTo: "",
};
const initialPageObj: IPageObj = {
  showFilter: true,
  filter: {
    ...initialFilter,
  },
  tableSearch: `&isActive=true`,
  dropdownOptions: {
    instrumentType: [],
  },
  deleteReceipt: {
    showPopUp: false,
    uid: "",
  },
  sharePaymentDialog: false,
  editReceiptDialog: false,
  editReceipt: {
    ...initialNewReceiptData,
  },
};

const PageDeposit = (props: RouteProps) => {
  /*
    -------------------------------------------------------
      State
    -------------------------------------------------------
    */

  const [pageObj, setPageObj] = useState<IPageObj>({ ...initialPageObj });
  const [showDialogAPIError, setShowDialogAPIError] = useState(false);
  const [errorMessage, setErrorMessage] = useState("");
  const [toastMessage, setToastMessage] = useState("");
  const [toastMessageVariant, setToastMessageVariant] = useState(TOAST_STYLE_TYPE.PRIMARY);
  const [triggerToast, setTriggerToast] = useState(false);
  const [suggestionsTemplate, setSuggestionsTemplate] = useState<any>([{ name: "" }]);
  const [receiptMenuModel, setReceiptMenuModel] = useState<IMenuModal[]>([
    {
      className: "px-1.5 text-xs text-primary-labelColor h-6.5 hover:bg-primary-sapphireBlue-100",
      label: "Delete",
      receiptUid: "",
      command: function () {
        // onDeleteReceipt("");
        console.log("delete");
      },
    },
  ]);
  const dataTableRef = useRef<any>(null);
  const receiptDeleteMenuRef = useRef<any>(null);
  const toastMessageRef = useRef(null);
  const addReceiptRef = useRef<IAddNewReceiptDialogRef>(null);
  const dispatch = useDispatch();

  // --------------------------- global variables ---------------------- //

  /*
     -------------------------------------------------------
       useEffect
     -------------------------------------------------------
   */
  useEffect(() => {
    dataTableRef?.current?.search();
    getInstrumentType();
  }, []);

  useEffect(() => {
    if (errorMessage) {
      setShowDialogAPIError(true);
    }
  }, [errorMessage]);

  useEffect(() => {
    if (toastMessageRef?.current && toastMessage !== "") {
      // @ts-ignore: will be fixed in future
      toastMessageRef?.current?.showFunction();
      setToastMessage("");
    }
  }, [toastMessage, toastMessageVariant, triggerToast]);

  useEffect(() => {
    dataTableRef?.current?.search();
  }, [pageObj?.tableSearch]);

  /*
    -------------------------------------------------------
      Service Calls
    -------------------------------------------------------
  */

  /*
    -------------------------------------------------------
      Handlers
    -------------------------------------------------------
  */
  const handleFilterChange = (event: ChangeEvent<HTMLInputElement> | CalendarChangeParams) => {
    const { name, value } = event.target;
    setPageObj((prevObj) => ({
      ...prevObj,
      filter: {
        ...prevObj.filter,
        [name]: value,
      },
    }));
  };

  const handleAmountFilterChange = (event: ChangeEvent<HTMLInputElement>) => {
    const { name, value } = event.target;
    const unformattedValue: number | string = currencyFormatToNumberForFilter(value ?? "");
    setPageObj((prevObj) => ({
      ...prevObj,
      filter: {
        ...prevObj.filter,
        [name]:
          typeof unformattedValue === "number" ? numberFormatter(unformattedValue, "en-IN", 0) : "",
      },
    }));
  };

  const handleAutoCompleteDepositChange = (event: any) => {
    const { value } = event.target;
    if (typeof value === "object") {
      setPageObj((prevPageObj) => ({
        ...prevPageObj,
        filter: {
          ...prevPageObj.filter,
          // amountCollectedGreaterThenEqualTo:""
          [event.target.name]: { name: value.depositRefNo },
        },
      }));
    } else {
      setPageObj((prevPageObj) => ({
        ...prevPageObj,
        filter: {
          ...prevPageObj.filter,
          // amountCollectedGreaterThenEqualTo:""
          [event.target.name]: { name: value },
        },
      }));
    }
  };

  const handleAutoCompleteBankChange = (event: any, isAccount?: boolean) => {
    const { value } = event.target;
    if (typeof value === "object") {
      setPageObj((prevPageObj) => ({
        ...prevPageObj,
        filter: {
          ...prevPageObj.filter,
          // amountCollectedGreaterThenEqualTo:""
          [event.target.name]: { name: value.holderName, accountNumber: value.accountNumber },
        },
      }));
    } else {
      let updatedObj: IAutocomplete = {};
      if (isAccount) {
        updatedObj = {
          ...updatedObj,
          name: "",
          accountNumber: value,
        };
      } else {
        updatedObj = {
          ...updatedObj,
          name: value,
          accountNumber: "",
        };
      }
      setPageObj((prevPageObj) => ({
        ...prevPageObj,
        filter: {
          ...prevPageObj.filter,
          // amountCollectedGreaterThenEqualTo:""
          [event.target.name]: { ...updatedObj },
        },
      }));
    }
  };

  const handleAutoCompleteFilterChange = (event: any) => {
    const { value } = event.target;
    if (typeof value === "object") {
      setPageObj((prevPageObj) => ({
        ...prevPageObj,
        filter: {
          ...prevPageObj.filter,
          // amountCollectedGreaterThenEqualTo:""
          [event.target.name]: { name: value.lanId, uid: value.uid },
        },
      }));
    } else {
      setPageObj((prevPageObj) => ({
        ...prevPageObj,
        filter: {
          ...prevPageObj.filter,
          // amountCollectedGreaterThenEqualTo:""
          [event.target.name]: { name: value, uid: "" },
        },
      }));
    }
  };

  /* 
    --------------------------------------------------------
    Validators:
    --------------------------------------------------------
  */

  /*
    -------------------------------------------------------
      Button Click Handlers
    -------------------------------------------------------
  */
  const onReset = () => {
    setPageObj((prevPageObj) => ({
      ...prevPageObj,
      filter: { ...prevPageObj.filter, ...initialFilter },
    }));
  };

  const onSearch = () => {
    let search = ``;
    if (pageObj?.filter?.deposit?.name) {
      search = `${search}&depositRefNo=${pageObj?.filter?.deposit?.name}`;
    }
    if (currencyFormatToNumberForFilter(pageObj?.filter?.depositAmountGreaterThenEqualTo)) {
      search = `${search}&totalAmount:ge=${currencyFormatToNumberForFilter(
        pageObj?.filter?.depositAmountGreaterThenEqualTo
      )}`;
    }
    if (currencyFormatToNumberForFilter(pageObj?.filter?.depositAmountLessThenEqualTo)) {
      search = `${search}&totalAmount:le=${currencyFormatToNumberForFilter(
        pageObj?.filter?.depositAmountLessThenEqualTo
      )}`;
    }
    if (pageObj?.filter?.dateOfDepositGreaterThenEqualTo) {
      search = `${search}&depositDate:ge=${new Date(
        pageObj?.filter?.dateOfDepositGreaterThenEqualTo
      )?.toISOString()}`;
    }
    if (pageObj?.filter?.dateOfDepositLessThenEqualTo) {
      search = `${search}&depositDate:le=${new Date(
        pageObj?.filter?.dateOfDepositLessThenEqualTo
      )?.toISOString()}`;
    }
    if (pageObj?.filter?.sahayyaBankAccount?.name) {
      search = `${search}&holderName=${pageObj?.filter?.sahayyaBankAccount?.name}`;
    }
    if (pageObj?.filter?.sahayyaBankAccount?.accountNumber) {
      search = `${search}&accountNumber=${pageObj?.filter?.sahayyaBankAccount?.accountNumber}`;
    }
    if (pageObj?.filter?.loan?.name) {
      search = `${search}&lanId=${pageObj?.filter?.loan?.name}`;
    }
    // if (pageObj?.filter?.status) {
    //   search = `${search}&status=${pageObj?.filter?.status}`;
    // }
    if (pageObj?.filter?.updatedOnGreaterThenEqualTo) {
      search = `${search}&updatedAtSystemDate:ge=${new Date(
        pageObj?.filter?.updatedOnGreaterThenEqualTo
      )?.toISOString()}`;
    }
    if (pageObj?.filter?.updatedOnLessThenEqualTo) {
      search = `${search}&updatedAtSystemDate:le=${new Date(
        pageObj?.filter?.updatedOnLessThenEqualTo
      )?.toISOString()}`;
    }
    setPageObj((prevPageObj) => ({
      ...prevPageObj,
      tableSearch: search,
    }));
  };

  const onToggleFilter = () => {
    setPageObj((prevPageObj) => ({
      ...prevPageObj,
      showFilter: !prevPageObj.showFilter,
    }));
  };

  const onToggleSharePymtDialog = () => {
    setPageObj((prevPageObj) => ({
      ...prevPageObj,
      sharePaymentDialog: !prevPageObj.sharePaymentDialog,
    }));
  };

  const onCreate = () => {
    // setPageObj((prevPageObj) => ({
    //   ...prevPageObj,
    //   editReceiptDialog: true,
    // }));
    props.navigate(LINK_FEES_DEPOSIT_CREATE_EDIT);
  };

  const onToggleReceiptDeleteMenu = (e: any, uid: string) => {
    receiptDeleteMenuRef?.current?.toggle?.(e);

    setReceiptMenuModel((prevObj) => {
      const updatedMenuModel = [...prevObj];
      updatedMenuModel[0] = {
        ...updatedMenuModel[0],
        command: () => {
          onDeleteReceipt(uid);
        },
      };
      return updatedMenuModel;
    });
  };

  const onResetWarningPop = () => {
    setPageObj((prevPageObj) => ({
      ...prevPageObj,
      deleteReceipt: {
        ...prevPageObj.deleteReceipt,
        showPopUp: false,
        uid: "",
      },
    }));
  };

  const onDeleteReceiptConfirm = async () => {
    // call delete api
    try {
      await FeeService.deleteDdeReceipt(pageObj.deleteReceipt.uid);
      setToastMessageVariant(TOAST_STYLE_TYPE.PRIMARY);
      setToastMessage(STATIC_MASTER_TYPE_TOAST_TEXT.SAVED);
      setTriggerToast(!triggerToast);
      dataTableRef?.current?.search();
      onResetWarningPop();
      // addReceiptRef.current?.onCloseAndClearData();
    } catch (err) {
      console.log(err);
      setErrorMessage(parseStandardAPIErrorMessage(err));
    }
  };

  const onEditReceipt = (rawData: ILoginFeeReceiptRowData) => {
    setPageObj((prevData) => ({
      ...prevData,
      editReceipt: {
        ...prevData.editReceipt,
        ...updateReceiptDetailsPageObj(rawData),
      },
    }));
  };

  const onResetEditReceipt = () => {
    setPageObj((pageObj) => ({
      ...pageObj,
      editReceipt: {
        ...pageObj?.editReceipt,
        ...initialNewReceiptData,
      },
      editReceiptDialog: false,
    }));
  };

  const onSaveNewReceipt = async (data: ILoginFeeReceiptData) => {
    const payload = enrichForNewReceipt(data);
    if (payload) {
      try {
        let service;
        if (payload?.uid) {
          service = FeeService.updateDdeReceipt;
        } else {
          service = FeeService.createDdeReceipt;
        }
        await service?.(payload);
        setToastMessageVariant(TOAST_STYLE_TYPE.PRIMARY);
        setToastMessage(STATIC_MASTER_TYPE_TOAST_TEXT.SAVED);
        setTriggerToast(!triggerToast);
        dataTableRef?.current?.search();
        addReceiptRef.current?.onCloseAndClearData();
        onResetEditReceipt();
      } catch (err) {
        console.log(err);
        setErrorMessage(parseStandardAPIErrorMessage(err));
      }
    }
  };

  /*
    -------------------------------------------------------
      Helper Functions
    -------------------------------------------------------
  */

  const onDeleteReceipt = (receiptUid: string) => {
    if (receiptUid) {
      setPageObj((prevObj) => ({
        ...prevObj,
        deleteReceipt: {
          ...prevObj.deleteReceipt,
          showPopUp: true,
          uid: receiptUid,
        },
      }));
    }
  };

  const searchBank = async (event: any) => {
    let suggestionsTemplate = [];
    // setIsLoading(true);
    try {
      await DepositAccountService.getBankAccount(0, 50, event.query).then((res) => {
        const options = res.data.results?.map((item: any) => ({
          holderName: item.bank.name,
          accountNumber: item.accountNumber,
          branch: item.ifsc.branch,
          uid: item.uid,
        }));
        if (res.status === 200) {
          suggestionsTemplate = [...(options ?? [])];
          // setIsLoading(false);
        }
      });
    } catch (error) {
      console.log(error);
    }

    if (suggestionsTemplate.length === 0) {
      suggestionsTemplate.push({ name: "", uid: "" });
    }

    setSuggestionsTemplate(suggestionsTemplate);
  };

  const searchAutoComplete = async (
    event: any,
    options: {
      service: any;
      field: string;
      search: string;
    }
  ) => {
    let suggestionsTemplate = [];
    await options
      ?.service?.(0, 50, `${options.field}:lk=${event.query}${options?.search}`)
      .then((res: any) => {
        if (res.status === 200) {
          suggestionsTemplate = [...res.data.results];
          // setIsLoading(false);
        }
      });
    if (suggestionsTemplate.length === 0) {
      suggestionsTemplate.push({ name: "" });
    }
    setSuggestionsTemplate(suggestionsTemplate);
  };

  const getInstrumentType = async () => {
    try {
      const res = await StaticMasterDataService.getDropdownList("FEE_MODE");
      const options: any = [];
      res?.data?.results.forEach((instrumentType: any) => {
        options.push({ name: instrumentType?.name ?? "", value: instrumentType?.code });
      });
      setPageObj((prevPageObj) => ({
        ...prevPageObj,
        dropdownOptions: {
          ...prevPageObj.dropdownOptions,
          instrumentType: options,
        },
      }));
    } catch (err) {
      console.error(err);
    }
  };

  /*
    -------------------------------------------------------
      Sectional Rendering
    -------------------------------------------------------
  */

  const itemTemplateDepositId = (item: any) => {
    if (item?.depositRefNo === undefined || item?.depositRefNo === "") {
      return (
        <div className="flex flex-col items-center justify-center h-39 pointer-events-none cursor-default">
          <Text className="text-primary-labelColor mt-2" label="label.noDataFound" />
        </div>
      );
    }
    return (
      <div className="flex flex-col">
        <Text className="text-primary-labelColor" label={`${item.depositRefNo}`} />
      </div>
    );
  };

  const itemTemplateBranch = (item: any) => {
    if (item?.uid === undefined || item?.uid === "") {
      return (
        <div className="flex flex-col items-center justify-center h-39 pointer-events-none cursor-default">
          <Text className="text-primary-labelColor mt-2" label="label.noDataFound" />
        </div>
      );
    }
    return (
      <div className="flex flex-col">
        <Text className="text-primary-labelColor" label={`${item.name}`} />
        {/* <Text className="text-secondary-gray-500 text-xxs" label={item.email} /> */}
      </div>
    );
  };

  const itemTemplateReceipt = (item: any) => {
    if (item?.uid === undefined || item?.uid === "") {
      return (
        <div className="flex flex-col items-center justify-center h-39 pointer-events-none cursor-default">
          <Text className="text-primary-labelColor mt-2" label="label.noDataFound" />
        </div>
      );
    }
    return (
      <div className="flex flex-col">
        <Text className="text-primary-labelColor" label={`${item.name}`} />
      </div>
    );
  };

  const itemTemplateApplicantName = (item: any) => {
    if (item?.uid === undefined || item?.uid === "") {
      return (
        <div className="flex flex-col items-center justify-center h-39 pointer-events-none cursor-default">
          <Text className="text-primary-labelColor mt-2" label="label.noDataFound" />
        </div>
      );
    }
    return (
      <div className="flex flex-col">
        <Text className="text-primary-labelColor" label={`${item.name}`} />
      </div>
    );
  };

  const itemTemplateSahayyaBankAccount = (item: any) => {
    if (item?.holderName === undefined || item?.holderName === "") {
      return (
        <div className="flex flex-col items-center justify-center h-39 pointer-events-none cursor-default">
          <Text className="text-primary-labelColor mt-2" label="label.noDataFound" />
        </div>
      );
    }
    return (
      <div className="flex flex-col">
        <Text className="text-primary-labelColor" label={`${item.holderName}`} />
      </div>
    );
  };

  const itemTemplateAccountNo = (item: any) => {
    if (item?.accountNumber === undefined || item?.accountNumber === "") {
      return (
        <div className="flex flex-col items-center justify-center h-39 pointer-events-none cursor-default">
          <Text className="text-primary-labelColor mt-2" label="label.noDataFound" />
        </div>
      );
    }
    return (
      <div className="flex flex-col">
        <Text className="text-primary-labelColor" label={`${item.accountNumber}`} />
      </div>
    );
  };

  const itemTemplateLanId = (item: any) => {
    if (item?.uid === undefined || item?.uid === "") {
      return (
        <div className="flex flex-col items-center justify-center h-39 pointer-events-none cursor-default">
          <Text className="text-primary-labelColor mt-2" label="label.noDataFound" />
        </div>
      );
    }
    return (
      <div className="flex flex-col">
        <Text className="text-primary-labelColor" label={`${item?.lanId}`} />
      </div>
    );
  };

  const renderFilter = () => {
    return (
      <div className="p-3 flex flex-col gap-6 bg-primary-sapphireBlue-100 rounded-lg">
        <div className="flex ">
          <SectionTitle title="text.masterData.types.search" />
        </div>
        <div className=" grid grid-cols-1 md:grid-cols-4 gap-3">
          <div>
            <AutoComplete
              label="label.fees.depositsId"
              name="deposit"
              field="name"
              value={pageObj?.filter?.deposit?.name}
              variant={INPUT_STYLE_TYPE.PRIMARY}
              suggestions={suggestionsTemplate}
              completeMethod={(e: any) =>
                searchAutoComplete(e, {
                  field: "&depositRefNo",
                  search: "",
                  service: FeeService.getFeeCollectionDepositList,
                })
              }
              onChange={handleAutoCompleteDepositChange}
              itemTemplate={itemTemplateDepositId}
            />
          </div>
          <div>
            <InputBox
              label="label.fees.depositAmount>="
              name="depositAmountGreaterThenEqualTo"
              value={pageObj?.filter?.depositAmountGreaterThenEqualTo}
              variant={INPUT_STYLE_TYPE.SECONDARY}
              onChange={handleAmountFilterChange}
            />
          </div>
          <div className="grid-group">
            <div className="mb-1 h-4.75 flex">
              <span className="font-sans text-primary-labelColor text-xs">
                Deposit Amount &lt;=
              </span>
            </div>
            <InputBox
              // label="label.fees.depositAmount>="
              hideLabel
              name="depositAmountLessThenEqualTo"
              value={pageObj?.filter?.depositAmountLessThenEqualTo}
              variant={INPUT_STYLE_TYPE.SECONDARY}
              onChange={handleAmountFilterChange}
            />
          </div>
          <div className="grid-group">
            <SystemDatePicker
              label={"label.fees.dateOfDeposit>="}
              name="instrumentDateGreaterThenEqualTo"
              onChange={handleFilterChange}
              value={
                pageObj?.filter?.dateOfDepositGreaterThenEqualTo
                  ? new Date(pageObj?.filter?.dateOfDepositGreaterThenEqualTo)
                  : null
              }
              variant={INPUT_STYLE_TYPE.SECONDARY}
            />
          </div>
          <div className="grid-group">
            <div className="mb-1 h-4.75 flex">
              <span className="font-sans text-primary-labelColor text-xs">
                Date of Deposit &lt;=
              </span>
            </div>
            <SystemDatePicker
              hideLabel
              name="instrumentDateGreaterThenEqualTo"
              onChange={handleFilterChange}
              value={
                pageObj?.filter?.dateOfDepositLessThenEqualTo
                  ? new Date(pageObj?.filter?.dateOfDepositLessThenEqualTo)
                  : null
              }
              variant={INPUT_STYLE_TYPE.SECONDARY}
            />
          </div>

          <div>
            <AutoComplete
              label="label.fees.sahayyaBankAccount"
              name="sahayyaBankAccount"
              field="name"
              value={pageObj?.filter?.sahayyaBankAccount?.name}
              variant={INPUT_STYLE_TYPE.PRIMARY}
              suggestions={suggestionsTemplate}
              completeMethod={searchBank}
              onChange={handleAutoCompleteBankChange}
              itemTemplate={itemTemplateSahayyaBankAccount}
            />
          </div>
          <div>
            <AutoComplete
              label="label.fees.accountNo"
              name="sahayyaBankAccount"
              field="name"
              value={pageObj?.filter?.sahayyaBankAccount?.accountNumber}
              variant={INPUT_STYLE_TYPE.PRIMARY}
              suggestions={suggestionsTemplate}
              completeMethod={(e: any) =>
                searchAutoComplete(e, {
                  field: "&accountNumber",
                  search: "",
                  service: DepositAccountService.getBankDetails,
                })
              }
              onChange={(e) => handleAutoCompleteBankChange(e, true)}
              itemTemplate={itemTemplateAccountNo}
            />
          </div>
          <div>
            <AutoComplete
              label="label.fees.loanApplicationNo."
              name="loan"
              field="name"
              value={pageObj?.filter?.loan?.name}
              variant={INPUT_STYLE_TYPE.PRIMARY}
              suggestions={suggestionsTemplate}
              completeMethod={(e: any) =>
                searchAutoComplete(e, {
                  field: "&lanId",
                  search: "&loanStatus:nq=DRAFT",
                  service: LoanService.getAll,
                })
              }
              onChange={handleAutoCompleteFilterChange}
              itemTemplate={itemTemplateLanId}
            />
          </div>
        
          <div className="grid-group">
            <SystemDatePicker
              label={"label.fees.updatedOn>="}
              name="updatedOnGreaterThenEqualTo"
              onChange={handleFilterChange}
              value={
                pageObj?.filter?.updatedOnGreaterThenEqualTo
                  ? new Date(pageObj?.filter?.updatedOnGreaterThenEqualTo)
                  : null
              }
              variant={INPUT_STYLE_TYPE.SECONDARY}
            />
          </div>
          <div className="grid-group">
            <div className="mb-1 h-4.75 flex">
              <span className="font-sans text-primary-labelColor text-xs">Updated On &lt;=</span>
            </div>
            <SystemDatePicker
              hideLabel
              name="updatedOnLessThenEqualTo"
              onChange={handleFilterChange}
              value={
                pageObj?.filter?.updatedOnLessThenEqualTo
                  ? new Date(pageObj?.filter?.updatedOnLessThenEqualTo)
                  : null
              }
              variant={INPUT_STYLE_TYPE.SECONDARY}
            />
          </div>

          <div className="col-span-4 flex gap-5 justify-end">
            <ButtonBox buttonType={BUTTON_TYPE.RESET_OUTLINE} onClick={onReset} />
            <ButtonBox buttonType={BUTTON_TYPE.SEARCH_OUTLINE} onClick={onSearch} />
          </div>
        </div>
      </div>
    );
  };

  const renderDepositId = (rowData: IFeeCollectionDepositTableRowData) => {
    const URL =
      linkBasePath + linkAuthRoute + LINK_FEES_DEPOSIT_CREATE_EDIT + "/" + rowData.depositUid;
    return (
      <a className="flex items-center underline text-primary-sapphireBlue-900" href={URL}>
        {rowData?.depositRefNo}
      </a>
    );
  };

  const renderDepositAmount = (rowData: IFeeCollectionDepositTableRowData) => {
    return (
      <Text
        className=" text-xs text-secondary-labelColor "
        label={numberFormatter(rowData?.totalAmount, "en-IN", 0)}
      />
    );
  };

  const renderDateOfDeposit = (rowData: IFeeCollectionDepositTableRowData) => {
    return (
      <Text
        className=" text-xs text-secondary-labelColor "
        label={formatDate(rowData?.depositDate)}
      />
    );
  };

  const renderSahayyaBankAccountNo = (rowData: IFeeCollectionDepositTableRowData) => {
    return (
      <Text
        className=" text-xs text-secondary-labelColor "
        label={`${rowData?.holderName} - ${rowData?.accountNumber}`}
      />
    );
  };

  const renderNoOfReceipts = (rowData: IFeeCollectionDepositTableRowData) => {
    return <Text className=" text-xs text-secondary-labelColor " label={rowData?.receiptCount} />;
  };

  const renderLoanApplicationNos = (rowData: IFeeCollectionDepositTableRowData) => {
    return <Text className=" text-xs text-secondary-labelColor " label={rowData?.lanId} />;
  };

  const renderUpdatedOn = (rowData: IFeeCollectionDepositTableRowData) => {
    return (
      <Text
        className=" text-xs text-secondary-labelColor "
        label={formatDate(rowData?.updatedAtSystemDate)}
      />
    );
  };

  const renderDepositReceipt = (rowData: IFeeCollectionDepositTableRowData) => {
    return <IconButton buttonType={BUTTON_TYPES.VIEW} />; // will open media gallery
  };

  const renderReceiptActions = (rowData: ILoginFeeReceiptRowData) => {
    return (
      <div className="grid grid-cols-2">
        <div className="">
          <IconButton
            icon={
              rowData.status === FEE_RECEIPT_STATUS.PENDING_DEPOSIT ? EditIcon : EditDisabledIcon
            }
            onClick={() => onEditReceipt(rowData)}
            tooltip="Edit"
            tooltipOptions={{
              position: "top",
              className: "text-xs font-Roboto bg-black text-white !p-1 rounded-md ",
              showDelay: "1000,",
              hideDelay: "300",
            }}
            disabled={rowData.status !== FEE_RECEIPT_STATUS.PENDING_DEPOSIT}
          />
        </div>
        <ButtonBox
          buttonType={BUTTON_TYPE.MENU}
          disabled={rowData.status !== FEE_RECEIPT_STATUS.PENDING_DEPOSIT}
          onClick={(e) => onToggleReceiptDeleteMenu(e, rowData.uid)}
        />
      </div>
    );
  };
  /*
    -------------------------------------------------------
    Main Rendering
    -------------------------------------------------------
  */

  return (
    <div className="p-3 rounded-masterDataPageRadius bg-white flex flex-col gap-3 loan-listing-layout-body-content">
      <div className="flex w-full justify-between items-center">
        <Text
          label="label.fees.deposits"
          className="text-primary-ptext-1000 text-h2 font-bold flex"
        />
        <div className="flex gap-10">
          <ButtonBox buttonType={BUTTON_TYPE.FILTER_OUTLINE} onClick={onToggleFilter} />
          <ButtonBox
            buttonType={BUTTON_TYPE.CREATE_NEW_DEPOSIT}
            className="create-button"
            onClick={onCreate}
          />
        </div>
      </div>
      {pageObj?.showFilter && renderFilter()}
      <ReadOnlyTable
        standardDataFormatPromise={FeeService.getFeeCollectionDepositList}
        dataKey="id"
        search={pageObj.tableSearch}
        ref={dataTableRef}
      >
        <Column
          className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6 min-w-35"
          header={useTranslateText("label.fees.depositsId")}
          body={renderDepositId}
        />
        <Column
          className="text-primary-ptext-1000 text-right text-xs  p-1 h-12 pl-6 "
          header={useTranslateText("label.fees.depositAmount")}
          body={renderDepositAmount}
        />
        <Column
          className="text-primary-ptext-1000 text-left text-xs w-1/12  p-1 h-12 pl-6"
          header={useTranslateText("label.fees.dateOfDeposit")}
          body={renderDateOfDeposit}
        />
        <Column
          className="text-primary-ptext-1000 text-left text-xs p-1 h-12 pl-6"
          header={useTranslateText("label.fees.sahayyaBankAccountAccountNo")}
          body={renderSahayyaBankAccountNo}
        />
        <Column
          className="text-primary-ptext-1000 text-right text-xs  p-1 h-12 pl-6"
          header={useTranslateText("label.fees.noOfReceipt")}
          body={renderNoOfReceipts}
        />
        <Column
          className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
          header={useTranslateText("label.fees.loanApplicationNo(s)")}
          body={renderLoanApplicationNos}
        />
        <Column
          className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6 min-w-35 "
          header={useTranslateText("label.fees.updatedOn")}
          body={renderUpdatedOn}
        />
        {/* <Column
          className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
          header={useTranslateText("label.fees.depositReceipt")}
          body={renderDepositReceipt}
        /> */}
        {/* <Column
          className="text-primary-ptext-1000 text-left text-xs p-1 h-12 pl-6"
          header={useTranslateText("label.loanListing.actions")}
          body={renderActions}
        /> */}
      </ReadOnlyTable>

      <SharePaymentDetailsDialog
        visible={pageObj?.sharePaymentDialog}
        onHide={onToggleSharePymtDialog}
        instrumentTypeOptions={pageObj.dropdownOptions.instrumentType}
      />
      <AddNewReceiptDialog
        visible={pageObj?.editReceiptDialog || pageObj?.editReceipt?.uid ? true : false}
        onHide={onResetEditReceipt}
        instrumentTypeOptions={pageObj.dropdownOptions.instrumentType}
        module={MODULE_FOR_ADD_NEW_RECEIPT.FEES}
        data={{
          updateReceiptDetails: pageObj?.editReceipt,
        }}
        onSubmit={onSaveNewReceipt}
        ref={addReceiptRef}
      />
      <MenuBox
        ref={receiptDeleteMenuRef}
        model={receiptMenuModel}
        variant="SECONDARY"
        buttonClassName="!bg-transparent"
        className="width-full-menubox bg-white  w-max p-1.5 rounded-xl mt-0.5 border shadow-autoCompletePanel"
      />
      <WarningDialog
        visible={pageObj?.deleteReceipt?.showPopUp}
        className="w-76 !px-14"
        title={"heading.dde.fees.receiptDelete"}
        warningMessage={`description.dde.fees.receiptDelete`}
        onCancel={onResetWarningPop}
        onConfirm={() => {
          return onDeleteReceiptConfirm();
        }}
        onHide={() => {
          //
        }}
      />
      <ErrorDialog
        onHide={() => {
          setShowDialogAPIError(false);
          setErrorMessage("");
        }}
        visible={showDialogAPIError}
        errorMessage={errorMessage}
      />
    </div>
  );
};

export default withRouter(PageDeposit);
